import { MemberId, ProjectBindId, ProjectId } from '../value';

export class Member {
  readonly projectBindId: ProjectBindId;
  readonly projectId: ProjectId;
  readonly projectMemberId: MemberId;
  constructor(args: {
    projectBindId: ProjectBindId;
    projectId: ProjectId;
    projectMemberId: MemberId;
  }) {
    this.projectBindId = args.projectBindId;
    this.projectId = args.projectId;
    this.projectMemberId = args.projectMemberId;
  }
}
